body,html{
    padding : 0px;
    margin : 0px;
    background-color : #EEE;
    max-width: 100%;
    overflow-x : hidden;
}


*::-webkit-scrollbar-track
 {
     -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
     background-color: #F5F5F5;
 }
 *::-webkit-scrollbar
 {
     width: 10px;
     background-color: #F5F5F5;
 }
 
 *::-webkit-scrollbar-thumb
 {
     background-color: #000000;
     border: 2px solid #555555;
 }